"use client";
import React, { useState, Dispatch, SetStateAction } from 'react';

interface ToggleButtonProps {
  options: { value: 'monthly' | 'yearly'; label: string }[];
  selectedValue: 'monthly' | 'yearly';
  onValueChange: Dispatch<SetStateAction<'monthly' | 'yearly'>>;
  className?: string;
}

const ToggleButton: React.FC<ToggleButtonProps> = ({
  options,
  selectedValue,
  onValueChange,
  className,
}) => {
  return (
    <div className={`inline-flex bg-secondary border-2 border-teriary rounded-lg p-1 mb-12 ${className}`}>
      {options.map((option) => (
        <button
          key={option.value}
          onClick={() => onValueChange(option.value)}
          className={`px-6 py-2 rounded-md font-medium transition-colors duration-200 ${
            selectedValue === option.value ? 'bg-teriary text-white' : 'text-gray-400 hover:text-white'
          }`}
        >
          {option.label}
        </button>
      ))}
    </div>
  );
};

interface ToggleGroupProps {
  title?: string;
}

const ToggleGroup: React.FC<ToggleGroupProps> = ({ title }) => {
  const [billingPeriod, setBillingPeriod] = useState<'monthly' | 'yearly'>('monthly');

  const billingOptions: { value: 'monthly' | 'yearly'; label: string }[] = [
    { value: 'monthly', label: 'Monthly' },
    { value: 'yearly', label: 'Yearly' },
  ];

  return (
    <div>
      {title && <h2>{title}</h2>}
      <ToggleButton
        options={billingOptions}
        selectedValue={billingPeriod}
        onValueChange={setBillingPeriod}
      />
      {/* ... שאר הקוד שלך */}
    </div>
  );
};

export default ToggleGroup;
